<template>
  <tr>
    <td data-title="Ï£ºÎ¨∏Ïùº" class="order-date">{{ orderDate }}</td>
    <td data-title="Ï£ºÎ¨∏ÏûêID">{{ item['UserId'] | emptyValue }}</td>
    <td data-title="Ï£ºÎ¨∏Ïûê" class="orderer-name">{{ item['ordererName'] }}</td>
    <td data-title="ÏÉÅÌíàÎ™Ö" class="item-name">
      {{ this.item['details'][0]['itemId'] | itemName }}
    </td>
    <td data-title="ÏòµÏÖò" class="item-option">{{ itemOptionName }}</td>
    <td data-title="Ï£ºÎ¨∏Î©îÎ™®">{{ itemRequireMemo | emptyValue }}</td>
    <td
      data-title="Î∞úÏÜ°ÏÉÅÌÉú"
      class="btns_post"
      v-if="this.$store.state.order.clickedBtn == 'ready'"
    >
      <button
        @click="sendMailAndPostOrder"
        v-if="postResult == 'none' && loading == false"
      >
        {{ btnText }}
      </button>
      <button v-if="loading == true" class="btn_post-loading">Î∞úÏÜ° Ï§ë..</button>
      <template v-else>
        <button v-if="postResult == 'complete'" class="btn_post-complete">
          Î∞úÏÜ°ÏôÑÎ£å
        </button>
        <button v-else-if="postResult == 'fail'" class="btn_post-fail">
          Ï†ÑÏÜ°Ïã§Ìå®
        </button>
      </template>
    </td>
  </tr>
</template>

<script>
import { sendMail, dispatchOrder } from '@/api/order';

export default {
  props: {
    item: {
      type: Object,
    },
  },
  data() {
    return {
      postResult: 'none',
      loading: false,
    };
  },
  computed: {
    btnText() {
      return this.validateEmail ? 'Î∞úÏÜ°ÌïòÍ∏∞' : 'ÏÜ°Ïû•Îì±Î°ù';
    },
    validateEmail() {
      const reg = /.+@.+\..+/;
      return reg.test(this.item['details'][0]['RequireMemo']);
    },
    orderDate() {
      let splitDate = this.item['orderDate'].split('-');
      return splitDate[0].substr(2) + '/' + splitDate[1] + '/' + splitDate[2];
    },
    itemOptionName() {
      let str = this.item['details'][0]['itemOptionName'];
      if (str !== '') {
        let endIndex = this.item['details'][0]['itemOptionName'].indexOf('Íº≠');
        if (endIndex !== -1) {
          str = this.item['details'][0]['itemOptionName'].substring(
            0,
            endIndex - 1,
          );
        }
      }
      return str;
    },
    itemRequireMemo() {
      return this.item['details'][0]['RequireMemo'].trim();
    },
  },
  methods: {
    async postOrder() {
      const { data } = await dispatchOrder(
        this.item['OrderSerial'],
        this.item['details'][0]['DetailIdx'],
      );
      if (data.code == 'SUCCESS') {
        this.loading = false;
        this.postResult = 'complete';
      } else {
        this.postResult = 'fail';
      }
    },
    async sendMailAndPostOrder() {
      let sendResult = {};
      this.loading = true;
      if (this.validateEmail) {
        try {
          sendResult = await sendMail({
            items: [
              {
                itemId: this.item['details'][0]['itemId'],
                itemOptionName: this.itemOptionName,
              },
            ],
            toEmail: this.item['details'][0]['RequireMemo'],
          });
          console.log('Î©îÏùº Ï†ÑÏÜ° ÏôÑÎ£å');
        } catch (error) {
          console.log(error);
          this.loading = false;
          if (error.response.status == 400) this.postResult = 'fail';
        }
        if (sendResult?.status == 200) {
          this.postOrder();
        }
      } else {
        this.postOrder();
      }
    },
  },
  filters: {
    emptyValue(value) {
      return !value ? 'X' : value;
    },
    itemName(itemId) {
      switch (itemId) {
        case 5033569:
          return '2023 Ïã¨Ìîå ÌîåÎûòÎÑà';
        case 5033568:
          return '2023 Î™®Îçò ÌîåÎûòÎÑà';
        case 5033567:
          return '2023 ÌÉÄÏûÑÎùºÏù∏ ÌîåÎûòÎÑà';
        case 5033566:
          return '2023 Î®ºÏä¨Î¶¨&Îç∞ÏùºÎ¶¨ ÌîåÎûòÎÑà';
        case 5033565:
          return '31DAYS ÌîåÎûòÎÑà';
        case 5033562:
          return '3ÎÖÑ 5ÎÖÑ ÏùºÍ∏∞';
        case 5033564:
          return 'ÏÑ∏Î°úÌòï Ïù∏Îç±Ïä§ ÎÖ∏Ìä∏';
        case 5033563:
          return 'Í∞ÄÎ°úÌòï Ïù∏Îç±Ïä§ ÎÖ∏Ìä∏';
        case 5033560:
          return 'ÎèÖÏÑúÎÖ∏Ìä∏';
        case 5033558:
          return 'ÎìúÎùºÎßàÎÖ∏Ìä∏';
        case 5033557:
          return 'Î®ºÏä¨Î¶¨ Ìä∏ÎûòÏª§Î∂Å';
        case 5033561:
          return '180 Î≤†Ïù¥ÏßÅ ÎÖ∏Ìä∏';
        case 5033559:
          return '180 Ï≤¥Ïª§Î≥¥Îìú ÎÖ∏Ìä∏';
        case 5068583:
          return 'Î†àÏãúÌîºÎ∂Å';
        default:
          return '';
      }
    },
  },
};
</script>

